 h1 {
    text-align: center;
    font-weight: 300;
    color: #777
 }
 
 h1 span {
    font-weight: 600;
 }
 
 .projectsContainer {
    width: 920px;
    padding: 10px 0;
    margin: 70px 10px 20px 320px;
    position: relative;
    overflow: hidden;
    background-color: #D6FFFD;
    box-sizing: border-box;
    border-radius: 25px;
    border: 4px #00EEE1 solid ;
    outline: 5px solid #000B43;
    background: linear-gradient(to left, rgba(2, 40, 51, 0), rgb(185, 219, 228)), url(../Components/backgroundCards.jpg);
    background-size: contain; 
    z-index: 2;
 }
 
 .container:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -1px;
    width: 2px;
    height: 100%;
    z-index: 1;
    
 }
 
  .timeline-block {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -moz-box-pack: justify;
    justify-content: space-between;
    clear: both;
    /* background-color: #D6FFFD; */
    
  }
 
/*  .timeline-block-right {
    float: right;
 }
 
 .timeline-block-left {
    float: left;
    direction: rtl
 } */
 
 .marker {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 2px solid #030c1b;
    background: #4FC1E9;
    margin-top: 10px;
    margin-left: 50px;
    z-index: 3000;
    position: absolute;
 }
 
 .timeline-content {
    width: 50%;
    padding: 0 15px;
    border-left: #001166 solid 2px;
    z-index: 1;
    margin-left: 57px;
 }

 .timeline-image {
    width: 45%;
    padding: 0 10px;
    z-index: 1;
    margin: 10px 10px;
    float: left;
 }

 .ProjImg{
    width: 100%;
    height: auto;
    border: black solid 2px;
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
    box-sizing: border-box;
}
 
 .timeline-content h1 {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 30px;
    font-weight: 500;
    color: #001166; 
 }

 .timeline-content h3 {
    font-size: 20px;
    font-weight: 500;
    color: rgb(150, 7, 7); 
 }

 .timeline-content h4 {
    font-size: 20px;
    font-weight: 500;
    color: rgb(150, 7, 7); 
 }
 
 .timeline-content span {
    font-size: 15px;
    color: #0d6efd;
    margin-top: 0;
    margin-bottom: 0;
    
 }
 
 .timeline-content p {
    font-size: 8px;
    margin-top: 0;
    margin-bottom: 0;
 }
 
 .timeline-content .links{
    font-size: 15px;
    font-weight: 500;
    color: #0d6efd;
    margin-top: 0;
    margin-bottom: 0;
 }
 
